body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
#root{
  margin: 0;
  padding: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html{
  margin: 0;
  padding: 0;
}
section{
  margin: 0;
  padding: 0;
  height: 100vh;
  box-sizing: border-box;
}
h1{
  color:green;
}
.sidebar{
  height: 30%;
  float: left;
  width: 100%;
  display: flex;
  background-color: hotpink;
  justify-content: space-around;
  align-items: center;
}
.sidebar ul{
  display: flex;
  width: 30%;
  justify-content: space-around;
  list-style: none;
  box-sizing: border-box;
}
.sidebar ul li{
  width: 25%;
  margin: 3px;
  text-align: center;
  font-size: 1.2em;
}
.sidebar ul li a{
  cursor: pointer;
  text-decoration: none;
  color: rgb(82, 78, 78);
}
.sidebar button{
  max-width: 80px ;
  width: 100%;
  height: 40px;
  border: none;
  background-color: khaki;
  outline: none;
  cursor: pointer;
  font-size: 1.1em;
}

